@import url(https://fonts.googleapis.com/css2?family=Merriweather:ital,wght@0,300;0,400;0,700;0,900;1,300;1,400;1,700;1,900&family=Open+Sans:ital,wght@0,400;0,700;1,400;1,700&display=swap);
.pane{background-color:#1e1e1e;color:#dedbeb;height:100vh;width:240px;outline:none;border-right:1px solid #28222d}.pane .avatar{background:rgba(0,0,0,0.1);padding:2em 0.5em;text-align:center}.pane .avatar img{width:100px;border-radius:50%;overflow:hidden;border:4px solid #dedbeb;box-shadow:0 0 0 4px rgba(255,255,255,0.2)}.pane .avatar h2{font-weight:normal;margin-bottom:0;padding-top:20px;color:#23D18B}.pane .link{list-style:none;padding:0.5em 1em;margin:0;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center}.pane .link li{padding:0 0.5em;min-width:35px;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-content:center;align-content:center}.pane .link li a{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-content:center;align-content:center}.pane .link li img{width:25px}.pane .link li img.ic-github{-webkit-filter:invert(84%) sepia(32%) saturate(92%) hue-rotate(169deg) brightness(101%) contrast(84%);filter:invert(84%) sepia(32%) saturate(92%) hue-rotate(169deg) brightness(101%) contrast(84%)}.pane .link li img.ic-linkedin{-webkit-filter:invert(28%) sepia(98%) saturate(1232%) hue-rotate(187deg) brightness(95%) contrast(94%);filter:invert(28%) sepia(98%) saturate(1232%) hue-rotate(187deg) brightness(95%) contrast(94%)}.pane .link li img.ic-facebook{-webkit-filter:invert(42%) sepia(81%) saturate(3312%) hue-rotate(187deg) brightness(104%) contrast(90%);filter:invert(42%) sepia(81%) saturate(3312%) hue-rotate(187deg) brightness(104%) contrast(90%)}.pane nav{padding:0;margin:0;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.pane nav a{padding:0.75em 1em 0.75em 4em;font-size:0.95em;font-weight:regular;background-repeat:no-repeat;background-position:left 15px center;background-size:auto 20px;transition:all 0.15s linear;cursor:pointer;color:#dedbeb;text-decoration:none}.pane nav a.ic{padding-right:23px;background-repeat:no-repeat}.pane nav a.ic-home{background-image:url(/static/media/home-solid.e88e27ae.svg)}.pane nav a.ic-skill{background-image:url(/static/media/tools-solid.a1f432f2.svg)}.pane nav a.ic-resume{background-image:url(/static/media/file-solid.fd780032.svg)}.pane nav a.ic-project{background-image:url(/static/media/project-diagram-solid.6e221912.svg)}.pane nav a.ic-blog{background-image:url(/static/media/blog-solid.ebc0e323.svg)}.pane nav a:hover{background-color:rgba(0,0,0,0.1)}.pane nav a:focus{outline:none}.pane nav a.active{border-left:5px solid #fb5c8e;background-color:rgba(0,0,0,0.1)}@media screen and (max-width: 900px) and (min-width: 400px){body{padding-left:90px}.menu{width:90px}.menu .avatar{padding:0.5em;position:relative}.menu .avatar img{width:60px}.menu .avatar h2{opacity:0;position:absolute;top:50%;left:100px;margin:0;min-width:200px;border-radius:4px;background:rgba(0,0,0,0.4);-webkit-transform:translate3d(-20px, -50%, 0);transform:translate3d(-20px, -50%, 0);transition:all 0.15s ease-in-out}.menu .avatar:hover h2{opacity:1;-webkit-transform:translate3d(0px, -50%, 0);transform:translate3d(0px, -50%, 0)}.menu ul li{height:60px;background-position:center center;background-size:30px auto;position:relative}.menu ul li span{opacity:0;position:absolute;background:rgba(0,0,0,0.5);padding:0.2em 0.5em;border-radius:4px;top:50%;left:80px;-webkit-transform:translate3d(-15px, -50%, 0);transform:translate3d(-15px, -50%, 0);transition:all 0.15s ease-in-out}.menu ul li span:before{content:'';width:0;height:0;position:absolute;top:50%;left:-5px;border-top:5px solid transparent;border-bottom:5px solid transparent;border-right:5px solid rgba(0,0,0,0.5);-webkit-transform:translateY(-50%);transform:translateY(-50%)}.menu ul li:hover span{opacity:1;-webkit-transform:translate3d(0px, -50%, 0);transform:translate3d(0px, -50%, 0)}}@media screen and (max-width: 400px){body{padding-left:0}.menu{width:230px;box-shadow:0 0 0 100em rgba(0,0,0,0);-webkit-transform:translate3d(-230px, 0, 0);transform:translate3d(-230px, 0, 0);transition:all 0.3s ease-in-out}.menu .smartphone-menu-trigger{width:40px;height:40px;position:absolute;left:100%;background:#dedbeb}.menu .smartphone-menu-trigger:before,.menu .smartphone-menu-trigger:after{content:'';width:50%;height:2px;background:#fff;border-radius:10px;position:absolute;top:45%;left:50%;-webkit-transform:translate3d(-50%, -50%, 0);transform:translate3d(-50%, -50%, 0)}.menu .smartphone-menu-trigger:after{top:55%;-webkit-transform:translate3d(-50%, -50%, 0);transform:translate3d(-50%, -50%, 0)}.menu ul li{padding:1em 1em 1em 3em;font-size:1.2em}.menu:focus{-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0);box-shadow:0 0 0 100em rgba(0,0,0,0.6)}.menu:focus .smartphone-menu-trigger{pointer-events:none}}

.card{padding:20px;border:1px solid #28222d;min-width:150px;box-sizing:border-box;cursor:pointer}.card:hover{box-shadow:0px 0px 5px #28222d}.card h3{max-height:50px;color:#23D18B;white-space:nowrap;overflow:hidden;text-overflow:ellipsis}.card p{line-height:20px;height:40px;margin:20px 0px;font-size:0.9em;overflow:hidden;display:-webkit-box;-webkit-line-clamp:2;-webkit-box-orient:vertical}.card .tags{display:-webkit-flex;display:flex;grid-column-gap:5px;-webkit-column-gap:5px;column-gap:5px;grid-row-gap:10px;row-gap:10px;-webkit-justify-content:left;justify-content:left;-webkit-flex-wrap:wrap;flex-wrap:wrap;height:25px;font-size:0.75em;overflow:hidden;margin-bottom:20px}.card .tags a{padding:3px 8px;color:#fb5c8e;border:1px solid #fb5c8e;text-decoration:none}.card .tags a:hover{box-shadow:0px 0px 2px #fb5c8e}.card .card-footer{font-size:0.7em;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between}

.markdown{padding:40px 60px}.markdown h1{text-align:center}.markdown p{text-align:justify;padding:10px 0px;font-size:1.1em}.markdown ul{padding:10px 0px 10px 50px;display:grid;grid-template-columns:auto;grid-row-gap:10px;row-gap:10px;font-size:1.1em}.markdown h2{margin-top:10px}.markdown a{text-decoration:underline}.markdown .blog-header{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;color:#fb5c8e}.markdown .tags{margin-top:20px;display:grid;grid-template-columns:repeat(10, auto);-webkit-justify-content:end;justify-content:end;grid-column-gap:5px;-webkit-column-gap:5px;column-gap:5px;overflow:hidden;margin-bottom:20px}.markdown .tags a{border:1px solid #fb5c8e;padding:5px 10px;color:#fb5c8e;text-decoration:none;text-align:center}.markdown .tags a:hover{box-shadow:0px 0px 2px #fb5c8e}.all-tags{display:grid;grid-template-columns:repeat(5, 1fr);grid-column-gap:10px;-webkit-column-gap:10px;column-gap:10px;grid-row-gap:10px;row-gap:10px}.all-tags a{width:100%;padding:10px 0px;text-align:center}.all-tags a:hover{box-shadow:0px 0px 2px #fb5c8e}

*{padding:0;margin:0}#root{width:100vw;height:100vh;display:-webkit-flex;display:flex}#root main{-webkit-flex:1 1;flex:1 1}*{font-family:'Ubuntu', sans-serif}main{padding:10px 50px;background-color:#1e1e1e;color:#dedbeb;overflow:scroll}main a{color:#dedbeb;text-decoration:none}main h1{padding-bottom:20px}main .markdown-list{display:grid;grid-template-columns:repeat(2, 1fr);grid-column-gap:50px;-webkit-column-gap:50px;column-gap:50px;grid-row-gap:50px;row-gap:50px}h1,h2{color:#23D18B}

