@import '../../assets/css/variable.scss';

.pane{
    background-color: $background;
    color: $foreground;
	height:100vh;
	width:$menu-width-desktop;
	outline:none;
    border-right: 1px solid $black;
	
	.avatar{
		background:rgba(0,0,0,0.1);
		padding:2em 0.5em;
		text-align:center;
		
		img{
			width:100px;
			border-radius:50%;
			overflow:hidden;
			border:4px solid $foreground;
			box-shadow:0 0 0 4px rgba(255,255,255,0.2);
		}
		
		h2{
			font-weight:normal;
			margin-bottom:0;
            padding-top: 20px;
            color: $green;
		}
	}

    .link{
		list-style:none;
		padding:0.5em 1em;
		margin:0;
        display: flex;
        justify-content: center;

		li{
			padding:0 0.5em;
            min-width: 35px;
            display: flex;
            justify-content: center;
            align-content: center;

            a{
                display: flex;
                justify-content: center;
                align-content: center;
            }
            img{
                width: 25px;

                &.ic-github{
                    filter: invert(84%) sepia(32%) saturate(92%) hue-rotate(169deg) brightness(101%) contrast(84%);
                }
                &.ic-linkedin{
                    filter: invert(28%) sepia(98%) saturate(1232%) hue-rotate(187deg) brightness(95%) contrast(94%);
                }
                &.ic-facebook{
                    filter: invert(42%) sepia(81%) saturate(3312%) hue-rotate(187deg) brightness(104%) contrast(90%);
                }
            }
        }
    }
	
	nav{
		padding:0;
		margin:0;
        display: flex;
        flex-direction: column;

		a{
			padding:0.75em 1em 0.75em 4em;
			font-size:0.95em;
			font-weight:regular;
			background-repeat:no-repeat;
			background-position:left 15px center;
			background-size:auto 20px;
			transition:all 0.15s linear;
			cursor:pointer;
            color: $foreground;
            text-decoration: none;
			
            &.ic{
                padding-right: 23px;
                background-repeat: no-repeat;
            }
            &.ic-home{
                background-image: url($icon-url-home);
            }
			&.ic-skill{
				background-image:url($icon-url-skill);
			}
			&.ic-resume{
				background-image:url($icon-url-resume);
			}
			&.ic-project{
				background-image:url($icon-url-project);
			}
			&.ic-blog{
				background-image:url($icon-url-blog);
			}
			
			&:hover{
				background-color:rgba(0,0,0,0.1);
			}
			
			&:focus{
				outline:none;
			}

            &.active{
                border-left: 5px solid $red;
                background-color:rgba(0,0,0,0.1);
            }
		}
	}
}

@media screen and (max-width:900px) and (min-width:400px){
	body{
		padding-left:$menu-width-tablet;
	}
	.menu{
		width:$menu-width-tablet;
		
		.avatar{
			padding:0.5em;
			position:relative;
			img{
				width:$menu-width-tablet - 30px;
			}
			
			h2{
				opacity:0;
				position:absolute;
				top:50%;
				left:$menu-width-tablet + 10px;
				margin:0;
				min-width:200px;
				border-radius:4px;
				background:rgba(0,0,0,0.4);
				transform:translate3d(-20px,-50%,0);
				transition:all 0.15s ease-in-out;
			}
			
			&:hover{
				h2{
					opacity:1;
					transform:translate3d(0px,-50%,0);
				}
			}
		}
		
		ul{
			li{
				height:$menu-width-tablet/1.5;
				background-position:center center;
				background-size:30px auto;
				position:relative;

				span{
					//display:none;
					opacity:0;
					position:absolute;
					background:rgba(0,0,0,0.5);
					padding:0.2em 0.5em;
					border-radius:4px;
					top:50%;
					left:$menu-width-tablet - 10px;
					transform:translate3d(-15px,-50%, 0);
					transition:all 0.15s ease-in-out;
					
					&:before{
						content:'';
						width:0;
						height:0;
						position:absolute;
						top:50%;
						left:-5px;
						border-top:5px solid transparent;
						border-bottom:5px solid transparent;
						border-right:5px solid rgba(0,0,0,0.5);
						transform:translateY(-50%);
					}
				}
				
				&:hover{
					span{
						opacity:1;
						transform:translate3d(0px,-50%, 0);
					}
				}
			}
		}
	}
}

@media screen and (max-width:400px){
	body{
		padding-left:0;
	}
	
	.menu{
		width:$menu-width-smartphone;
		box-shadow:0 0 0 100em rgba(0,0,0,0);
		transform:translate3d(-$menu-width-smartphone,0,0);
		transition:all 0.3s ease-in-out;
		
		.smartphone-menu-trigger{
			width:40px;
			height:40px;
			position:absolute;
			left:100%;
			background:$foreground;
			
			&:before, &:after{
				content:'';
				width:50%;
				height:2px;
				background:#fff;
				border-radius:10px;
				position:absolute;
				top:45%;
				left:50%;
				transform:translate3d(-50%, -50%, 0);
			}
			
			&:after{
				top:55%;
				transform:translate3d(-50%, -50%, 0);
			}
		}
		
		ul{
			
			li{
				padding:1em 1em 1em 3em;
				font-size:1.2em;
			}
		}
		
		&:focus{
			transform:translate3d(0,0,0);
			box-shadow:0 0 0 100em rgba(0,0,0,0.6);
			
			.smartphone-menu-trigger{
				pointer-events:none;
			}
		}
	}
}
